<template>
  <div class="container">
    <!-- 头部标题 -->
    <navbar>
      <h4 slot="title">我的跟帖</h4>
    </navbar>
    <div class="reply-list" v-for="(item,index) in replylist" :key="index">
      <div class="date">{{moment(item.create_date).format("YYYY-MM-DD hh:mm:ss")}}</div>
      <!-- 有回复的评论需要展示 -->
      <div class="content" v-if="item.parent">
        <div class="author">{{item.parent.user.nickname}}</div>
        <div class="reply">{{item.parent.content}}</div>
      </div>

      <div class="myself">{{item.content}}</div>
      <router-link to="#" class="artile-link">原文:{{item.post.title}}</router-link>
    </div>
  </div>
</template>

<script>
import navbar from "@/components/navbar.vue";
// 引入第三方的日期格式处理的工具库
import moment from "moment";

export default {
  data() {
    return {
      replylist: [],
      moment
    };
  },
  mounted() {
    const info = JSON.parse(localStorage.getItem("user_info"));

    this.$axios({
      url: `/user_comments`,
      method: "get",
      // 添加验证token
      headers: {
        Authorization: info.token
      }
    }).then(res => {
      this.replylist = res.data.data;
      console.log(this.replylist);
    });
  },

  components: {
    navbar
  }
};
</script>

